:root {
    --side-bar-bg-color: #efefef;
    --control-text-color: #777;
    --primary-color: #393894;
    --primary-btn-border-color: #393894;
    --active-file-bg-color: #eaeaea;
    --active-file-text-color: inherit;
    --active-file-border-color: #393894;
    --item-hover-text-color: #393894;
}

.markdownShower {
    html {
        font-size: 0.925rem;
    }

    body {
        background: none;
        font-family: "Open Sans", "Clear Sans", "Helvetica Neue", Helvetica,
            Arial, sans-serif;
        color: #2e2e2e;
        line-height: 1.6;
        height: 100%;
    }

    #write {
        max-width: 990px;
        margin: 0 auto;
        padding: 30px;
        padding-bottom: 100px;
        position: static;
        width: 100%;
    }

    #write > ul:first-child,
    #write > ol:first-child {
        margin-top: 30px;
    }

    a {
        color: #1b69b6;
    }

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        position: relative;
        margin-top: 1rem;
        margin-bottom: 1rem;
        font-weight: bold;
        line-height: 1.4;
        cursor: text;
    }

    h1:hover a.anchor,
    h2:hover a.anchor,
    h3:hover a.anchor,
    h4:hover a.anchor,
    h5:hover a.anchor,
    h6:hover a.anchor {
        text-decoration: none;
    }

    h1 tt,
    h1 code {
        font-size: inherit;
    }

    h2 tt,
    h2 code {
        font-size: inherit;
    }

    h3 tt,
    h3 code {
        font-size: inherit;
    }

    h4 tt,
    h4 code {
        font-size: inherit;
    }

    h5 tt,
    h5 code {
        font-size: inherit;
    }

    h6 tt,
    h6 code {
        font-size: inherit;
    }

    h1 {
        padding-bottom: 0.3em;
        font-size: 2.25em;
        line-height: 1.2;
        border-bottom: 1px solid #eee;
    }

    h2 {
        padding-bottom: 0.3em;
        font-size: 1.75em;
        line-height: 1.225;
        border-bottom: 1px solid #eee;
    }

    h3 {
        font-size: 1.5em;
        line-height: 1.43;
    }

    h4 {
        font-size: 1.25em;
    }

    h5 {
        font-size: 1em;
    }

    h6 {
        font-size: 1em;
        color: #777;
    }

    p,
    blockquote,
    ul,
    ol,
    dl,
    table {
        margin: 0.8em 0;
    }

    ul li,
    ol li {
        line-height: 1.2;
    }

    li > ol,
    li > ul {
        margin: 0 0;
    }

    hr {
        height: 0.25em;
        padding: 0;
        margin: 24px 0;
        background-color: #e1e4e8;
        border: 0 none;
        overflow: hidden;
        box-sizing: content-box;
    }

    li p.first {
        display: inline-block;
    }

    ul,
    ol {
        padding-left: 30px;
    }

    ul:first-child,
    ol:first-child {
        margin-top: 0;
    }

    ul:last-child,
    ol:last-child {
        margin-bottom: 0;
    }

    blockquote {
        border-left: 4px solid #dfe2e5;
        font-family: "Hack";
        font-size: 13px;
        padding: 0 15px;
        color: #707070;
    }

    blockquote blockquote {
        font-family: "Hack";
        padding-right: 0;
    }

    table {
        font-size: 0.875rem;
        padding: 0;
        word-break: initial;
    }

    table tr {
        border-top: 1px solid #dfdfdf;
        margin: 0;
        padding: 0;
    }

    thead {
        background-color: #f8f8f8;
    }

    table tr th {
        font-weight: bold;
        border: 1px solid #dfdfdf;
        border-bottom: 0;
        border-bottom: solid 2px #ccc;
        margin: 0;
        padding: 10px 16px;
    }

    table tr td {
        border: 1px solid #dfe2e5;
        margin: 0;
        padding: 10px 16px;
    }

    table tr th:first-child,
    table tr td:first-child {
        margin-top: 0;
    }

    table tr th:last-child,
    table tr td:last-child {
        margin-bottom: 0;
    }

    .CodeMirror-lines {
        padding: 4px 0;
    }

    .code-tooltip {
        box-shadow: 0 1px 1px 0 rgba(0, 28, 36, 0.3);
        border-top: 1px solid #eef2f2;
    }

    .md-fences,
/* code, */
tt {
        border-radius: 3px;
        color: #b9bcba;
        padding: 0;
        font-size: 0.9em;
    }

    code {
        padding: 2px 4px;
        color: #c0341d;
        background-color: #fbe5e1;
        border-radius: 4px;
    }

    .md-fences {
        margin-bottom: 15px;
        margin-top: 15px;
        padding-top: 8px;
        padding-bottom: 6px;
    }

    .md-task-list-item > input {
        margin-left: -1.3em;
    }

    @media print {
        html {
            font-size: 13px;
        }

        table,
        pre {
            page-break-inside: avoid;
        }

        pre {
            word-wrap: break-word;
        }
    }

    .md-fences {
        background-color: #f8f8f8;
    }

    #write pre.md-meta-block {
        padding: 1rem;
        font-size: 85%;
        line-height: 1.45;
        background-color: #f7f7f7;
        border: 0;
        border-radius: 3px;
        color: #777777;
        margin-top: 0 !important;
    }

    .mathjax-block > .code-tooltip {
        bottom: 0.375rem;
    }

    .md-mathjax-midline {
        background: #fafafa;
    }

    #write > h3.md-focus:before {
        left: -1.5625rem;
        top: 0.375rem;
    }

    #write > h4.md-focus:before {
        left: -1.5625rem;
        top: 0.285714286rem;
    }

    #write > h5.md-focus:before {
        left: -1.5625rem;
        top: 0.285714286rem;
    }

    #write > h6.md-focus:before {
        left: -1.5625rem;
        top: 0.285714286rem;
    }

    .md-image > .md-meta {
        border-radius: 3px;
        padding: 2px 0px 0px 4px;
        font-size: 0.9em;
        color: inherit;
    }

    .md-tag {
        color: #a7a7a7;
        opacity: 1;
    }

    .md-toc {
        margin-top: 20px;
        padding-bottom: 20px;
    }

    .sidebar-tabs {
        border-bottom: none;
        font-weight: normal;
    }

    #typora-sidebar .sidebar-tabs {
        font-family: "Open Sans" !important;
        border-bottom: 1px solid #dfdfdf;
        font-weight: bold;
    }

    #typora-sidebar .file-list-item-file-name {
        font-family: "Open Sans";
        font-weight: bold;
    }

    #typora-sidebar .file-list-item-summary,
    #typora-sidebar .ty-search-item-line {
        font-family: "Open Sans";
    }

    #typora-sidebar #outline-content .outline-h1 > .outline-item {
        font-family: "Open Sans";
    }

    #typora-sidebar
        .file-tree-node.file-library-file-node.active
        .file-node-background,
    #typora-sidebar .file-list-item.file-library-file-node.active {
        border-left: 4px solid #393894;
    }

    .file-library-node:not(.file-node-root):focus > .file-node-content {
        outline: none;
    }

    #typora-sidebar .file-list-item.file-library-file-node:hover {
        background: #f1f1f1;
    }

    #typora-sidebar .file-tree-node.file-library-file-node {
        padding: 0;
    }

    #typora-sidebar
        .file-tree-node.file-library-file-node.active
        .file-node-title {
        color: #2a2971;
    }

    #typora-sidebar .file-tree-node {
        border: 0;
        padding: 0;
        margin: 0;
    }

    .file-node-icon {
        color: #707070;
        margin-right: 8px;
    }

    .active .file-node-icon {
        color: #2a2971;
    }

    .file-library-node.file-tree-node {
        padding: 0;
        margin: 0;
    }

    .file-node-background {
        padding: 25px 10px;
    }

    .file-node-content {
        padding: 15px 5px;
    }

    .file-node-content:hover {
        cursor: pointer;
    }

    .active .file-node-content {
        font-weight: bold;
    }

    #typora-quick-open {
        border: 1px solid #ddd;
        background-color: #f8f8f8;
    }

    #typora-quick-open-item {
        background-color: #fafafa;
        border-color: #fefefe #e5e5e5 #e5e5e5 #eee;
        border-style: solid;
        border-width: 1px;
    }

    /** focus mode */
    .on-focus-mode blockquote {
        border-left-color: rgba(85, 85, 85, 0.12);
    }

    header,
    .context-menu,
    .megamenu-content,
    footer {
        font-family: "Open Sans", "Arial", sans-serif;
    }

    .megamenu-opened header {
        background-image: none;
    }

    #typora-sidebar #ty-sidebar-footer {
        background-color: #f9f9f9 !important;
        border-top: 1px solid #dfdfdf;
    }

    .megamenu-menu-header {
        border-bottom: 1px solid #16161a;
    }

    .megamenu-menu-list li:not(.saved) a:not(.active):hover,
    .megamenu-menu-list li a.active {
        background: #101010 !important;
    }

    .file-node-content:hover .file-node-icon,
    .file-node-content:hover .file-node-open-state {
        visibility: visible;
    }

    .mac-seamless-mode #typora-sidebar {
        background-color: #f9f9f9;
        font-weight: 100;
    }

    .md-lang {
        color: #b4654d;
    }

    .html-for-mac .context-menu {
        --item-hover-bg-color: #e6f0fe;
    }

    #md-notification .btn {
        border: 0;
    }

    .dropdown-menu .divider {
        border-color: #e5e5e5;
    }

    .ty-preferences .window-content {
        background-color: #fafafa;
    }

    .ty-preferences .nav-group-item.active {
        color: white;
        background: #999;
    }

    /* CodeMirror Dracula theme */
    .cm-s-inner {
        border: 1px solid var(--accent-color) !important;
        border-radius: 0.25rem;
        font-family: "Hack";
    }

    .cm-s-inner.CodeMirror,
    .cm-s-inner .CodeMirror-gutters {
        background-color: #212425 !important;
        color: #f8f8f2 !important;
        border: none;
    }

    .CodeMirror {
        height: 300px;
        height: auto;
    }

    .CodeMirror-lines {
        padding: 4px 0;
        /* Vertical padding around content */
    }

    .CodeMirror div.CodeMirror-cursor {
        border-left: 1px solid green;
        z-index: 3;
    }

    .cm-s-inner .CodeMirror-gutters {
        background-color: #0d0e0f !important;
        border-right: 1px solid #444;
        border-radius: 4px;
        width: 5ch;
        color: #5c5d5c !important;
        height: 100%;
        white-space: nowrap;
    }

    .cm-s-inner .CodeMirror-cursor {
        border-left: solid rgb(96, 255, 96) !important;
    }

    .cm-s-inner .CodeMirror-linenumber {
        color: #5c5d5c;
        font-weight: bold;
        width: 4ch !important;
    }

    .cm-s-inner .CodeMirror-line::selection,
    .cm-s-inner .CodeMirror-line::-moz-selection,
    .cm-s-inner .CodeMirror-line > span::selection,
    .cm-s-inner .CodeMirror-line > span::-moz-selection,
    .cm-s-inner .CodeMirror-line > span > span::selection,
    .cm-s-inner .CodeMirror-line > span > span::-moz-selection {
        background: rgba(255, 255, 255, 0.1);
    }

    .cm-s-inner span.cm-comment {
        color: #969896;
    }

    .cm-s-inner span.cm-string,
    .cm-s-inner span.cm-string-2 {
        color: #e4eb9b;
    }

    .cm-s-inner span.cm-number {
        color: #c7b2e4;
    }

    .cm-s-inner span.cm-variable,
    .cm-s-inner span.cm-variable-2 {
        /* color: #50fa7b; */
        /* color: #a8b151; */
        color: #81a2be;
    }

    .cm-s-inner span.cm-def {
        color: white;
    }

    .cm-s-inner span.cm-operator {
        color: #ff79c6;
    }

    .cm-s-inner span.cm-keyword {
        color: #e79ac6;
        color: #b294bb;
    }

    .cm-s-inner span.cm-atom {
        color: #bd93f9;
    }

    .cm-s-inner span.cm-meta {
        color: #f8f8f2;
    }

    .cm-s-inner span.cm-tag {
        color: #8abeb7;
    }

    .cm-s-inner span.cm-attribute {
        color: #91eba7;
    }

    .cm-s-inner span.cm-qualifier {
        color: #82ee9d;
    }

    .cm-s-inner span.cm-property {
        /* color: #66d9ef; */
        color: #7091b1;
        color: #81a2be;
    }

    .cm-s-inner span.cm-builtin {
        color: #8ffaaa;
    }

    .cm-s-inner span.cm-variable-3,
    .cm-s-inner span.cm-type {
        color: #f1c290;
    }

    .md-fences.md-focus .cm-s-inner .CodeMirror-activeline-background {
        background: rgba(255, 255, 255, 0.1);
    }

    .cm-s-inner .CodeMirror-matchingbracket {
        text-decoration: underline;
        color: white !important;
    }

    .cm-s-inner .CodeMirror-selected,
    .cm-s-inner .CodeMirror-selectedtext {
        background: #3b4254 !important;
        color: inherit;
        text-shadow: none;
    }

    #write > p:first-child:empty {
        position: static;
    }

    #write > p:first-child:not(:only-child)::before,
    #write > p:only-child::before {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        // background: url("gitlab/logo.png") no-repeat 45% 90% transparent;
        background-size: cover;
        opacity: 0;
        height: 100%;
        max-width: 100%;
        width: 100%;
    }

    #write > p:only-child:empty::before {
        opacity: 1;
    }
}
